.containerButtons{
    margin-top: 100px;
}
.menu_button1 {
    position: absolute;
    top:0;
    width: 15vw;
    padding: 1.5vw 1.5vh;
    font-family: 'Fjalla One', sans-serif;
    background-color: rgb(26, 128, 192);
    color: #FFF;
    font-size: 1.5vw;
    text-decoration: none;
    border: 2px solid #FFF;
    border-radius: 10px;
    outline: none;
}

.menu_button1 {
    left: 0;
}


.menu_button1:hover{
    box-shadow: inset 0 0 8px #000;
}

/*TOGGLE*********************************************/    

.button_up_deposit {
    animation: fadeButtonUp 2s ease-out forwards;
}

@keyframes fadeButtonUp{
    0%{}
    50%{margin-top:-1.5vh; width:18vw; background-color: rgb(26, 128, 192);}
    100%{margin-top:-1.5vh; width:100%; background-color: rgb(26, 128, 192);}
}

.button_up_take {
    animation: fadeButtonUpTake 2s ease-out forwards;
}

@keyframes fadeButtonUpTake{
    0%{}
    50%{margin-top:-2.5vh; left:20vw; width:18vw; background-color: #0F1020}
    100%{margin-top:-2.5vh; left:0; width:100%; background-color: #0F1020}
}

.button_up_transfer {
    animation: fadeButtonUpTransfer 2s ease-out forwards;
}

@keyframes fadeButtonUpTransfer{
    0%{}
    50%{margin-top:-2.5vh; left:40vw; width:18vw; background-color: #0F1020}
    100%{margin-top:-2.5vh; left:0; width:100%; background-color: #0F1020}
}

.button_up_more {
    animation: fadeButtonUpMore 2s ease-out forwards;
}

@keyframes fadeButtonUpMore{
    0%{}
    50%{margin-top:-2.5vh; left:60vw; width:7vw; background-color: #0F1020}
    100%{margin-top:-2.5vh; left:0; width:100%; background-color: #0F1020}
}


@keyframes fadeLoaded{
    0%{opacity: 1}
    100%{opacity: 0.1}
}

.menu_button_out {
    animation: fadeLoadedButtons 1s ease-out forwards;
}

@keyframes fadeLoadedButtons{
    0%{opacity: 1}
    90%{margin-top:-2.5vh; opacity: 0.5}
    100%{margin-top:-2.5vh; opacity: 0;}
}