.col-container {
    display: flex;
  }
  .no-gutters {
    display: inline-flex;
    border: 1px solid lightgrey;
    padding: 50px 20px 0 0;
    margin-bottom: 10px;
    border-radius: 15px;
    box-shadow: rgba(0, 0, 0, 0.8) 0 0 5px;
    border-collapse: collapse;
  }

  .no-gutters {
    align-items: center;
    margin-top: 50px;
  }
  
  h5 {
    font-family: "Titillium Web", sans-serif;
    color: #229acd;
  }
  
  .typeSize {
    font-style: italic;
    font-weight: bold;
    margin-top: 15px;
    font-size: 1.5em;
    font-family: "Titillium Web", sans-serif;
  }
  .detailItem {
    margin: 0.5em auto;
  }
  