/*CALCULATOR SECTION*****/

.calc {
    display: none;
}

.calcShow {
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    margin: 0 10%;
    width: 100%;
    min-height: 10vh;
}

/*NUMBERS*/

.numbers {
    margin-top: 2vh;
    width: 50%;
    cursor: pointer;
}

.numbersAnimation {
    animation: calcShowAnimation 4s ease-out forwards;
    
}

@keyframes calcShowAnimation{
    0%{margin-top:-15%; opacity:0}
    50%{margin-top:-15%; opacity:0}
    100%{margin-top:0; opacity:1}
}

.number {
	width:20%;
    min-height: 7vh;
    margin: 0.8vh 0.5vw;

    border: none;
    border-radius: 5px;
    outline: none;

	background-color: white;
    font-size: 2vw;
    font-weight: bold;

    box-shadow: 2px 2px 3px #000;
}

.number:active {
    box-shadow: inset 0 0 4px #000;
    
}

.ok {
    background: white;
    color:white;
}

.toastPin{
    margin-left: 30px;
}