/*TOGGLE*********************************************/    

.button_up_deposit {
    animation: fadeButtonUp 2s ease-out forwards;
}

@keyframes fadeButtonUp{
    0%{}
    50%{margin-top:-1.5vh; width:18vw; background-color: rgb(26, 128, 192);}
    100%{margin-top:-1.5vh; width:100%; background-color: rgb(26, 128, 192);}
}

.button_up_take {
    animation: fadeButtonUpTake 2s ease-out forwards;
}

@keyframes fadeButtonUpTake{
    0%{}
    50%{margin-top:-2.5vh; left:20vw; width:18vw; background-color: #0F1020}
    100%{margin-top:-2.5vh; left:0; width:100%; background-color: #0F1020}
}

