/*QUANTITY*/

.showQuantityRetiro {
    position: relative;
    width: 80%;
    min-height:39vh;
}

.toppRetiro {
    animation: fadeinCantidad 4s ease-out forwards;
}
.buttonsRetiro{
    width:100%;
    margin-left:30%;
}

@keyframes fadeinCantidad {
    0%{margin:5% -100% 0 100%; opacity:0}
    50%{margin:5% -100% 0 100%; opacity:0}
    100%{margin-top:5%; opacity:1}
}

.quantityRetiro {
    position: absolute;
    width: 100%;
    bottom: 0;
    text-align: right;
}

.quantity_sizeRetiro {
    animation: fadeinQuantity 4s ease-out forwards;
    font-size: 6rem;
}

@keyframes fadeinQuantity {
    0%{margin:0 -50% 0 50%; opacity:0}
    50%{margin:0 -50% 0 50%; opacity:0}
    100%{margin:0; opacity:1}
}

.quantity_hrRetiro {
    width: 100%;
    animation: fadeinQuantityHr 4s ease-out forwards;
}

@keyframes fadeinQuantityHr {
    0%{margin:0 -100% 0 100%; width:0%; opacity: 0}
    50%{margin:0 -100% 0 100%; width:0%; opacity: 0}
    100%{margin:0; opacity:1}
}
