
.hrAnimationRetiro {
    animation: fadeinHr 2s ease-out 1;
}

/* keyboard css */
/*CALCULATOR SECTION*****/

.calc {
    display: none;
}

.calcShow {
    display: flex;
    flex-wrap: wrap;
    position: absolute;
    width: 100%;
    min-height: 10vh;
}

/*NUMBERS*/

.numbers {
    margin-top: 2vh;
    width: 50%;
    cursor: pointer;
}

.numbersAnimation {
    animation: calcShowAnimation 4s ease-out forwards;
    
}

@keyframes calcShowAnimation{
    0%{margin-top:-15%; opacity:0}
    50%{margin-top:-15%; opacity:0}
    100%{margin-top:0; opacity:1}
}

.number {
	width:26%;
    min-height: 7vh;
    margin: 0.8vh 0.5vw;

    border: none;
    border-radius: 5px;
    outline: none;

	background-color: white;
    font-size: 2vw;
    font-weight: bold;

    box-shadow: 2px 2px 3px #000;
}

.number:active {
    box-shadow: inset 0 0 4px #000;
    
}

.ok {
    background: transparent;
    color:white;
}

.toastPin{
    margin-left: 30px;
}

/*QUANTITY*/

.showQuantityRetiro {
    position: relative;
    width: 80%;
    min-height:39vh;
}

.toppRetiro {
    animation: fadeinCantidad 4s ease-out forwards;
}
.buttonsRetiro{
    width:100%;
    margin-left:30%;
}

@keyframes fadeinCantidad {
    0%{margin:5% -100% 0 100%; opacity:0}
    50%{margin:5% -100% 0 100%; opacity:0}
    100%{margin-top:5%; opacity:1}
}

.quantityRetiro {
    position: absolute;
    width: 100%;
    bottom: 0;
    text-align: right;
}

.quantity_sizeRetiro {
    animation: fadeinQuantity 4s ease-out forwards;
    font-size: 6rem;
}

@keyframes fadeinQuantity {
    0%{margin:0 -50% 0 50%; opacity:0}
    50%{margin:0 -50% 0 50%; opacity:0}
    100%{margin:0; opacity:1}
}

.quantity_hrRetiro {
    width: 100%;
    animation: fadeinQuantityHr 4s ease-out forwards;
}

@keyframes fadeinQuantityHr {
    0%{margin:0 -100% 0 100%; width:0%; opacity: 0}
    50%{margin:0 -100% 0 100%; width:0%; opacity: 0}
    100%{margin:0; opacity:1}
}